@import url(https://fonts.googleapis.com/css?family=Arvo:400,700);
@font-face {
  font-family: 'FontAwesome';
  src: url('https://use.fontawesome.com/releases/v5.8.1/webfonts/fa-solid-900.eot');
  src: url('https://use.fontawesome.com/releases/v5.8.1/webfonts/fa-solid-900.woff') format("woff"),
       url('https://use.fontawesome.com/releases/v5.8.1/webfonts/fa-solid-900.ttf') format("truetype"),
       url('https://use.fontawesome.com/releases/v5.8.1/webfonts/fa-solid-900.svg') format("svg");
}

html, body, #root {
  height: 100%;
  min-height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Arvo', 'Palatino', 'Helvetica', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.App {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 100%;
}

main {
  display: flex;
  flex-direction: row;
    justify-content: center;
}

main div {
  width: 10%;
  height: 2em;
  padding: 1em;
  margin: 1em;

  display: flex;
  justify-content: center;
  align-items: center;

  font-family: 'FontAwesome';
  color: #fff;
  cursor: pointer;

  border-radius: 10px;
  border: 2px solid black;
}

main div.heart {
  background-color: #FFDC00;
}
main div.heart:before {
  content: '\f004';
  font-size: 2em;
  padding: 0.5em;
}

main div.heart-broken {
  background-color: #000;
}
main div.heart-broken:before {
  content: '\f7a9';
  font-size: 2em;
  padding: 0.5em;
}


@media only screen and (min-width: 426px) {
  img {
    width: 20em;
  }
}

